import { Divider, Descriptions, Button } from 'antd';
import dayjs from 'dayjs';

const exchangeMap = {
  SSE: '',
  SZSE: '',
  HKEX: '',
};

const BaseInfo = (props) => {
  const {
    ts_code: code,
    name,
    website,
    employees,
    manager,
    market,
    exchange,
    list_date,
    act_name,
    act_ent_type,
    province,
    city,
    main_business,
    l1_name,
    l2_name,
    l3_name,
  } = props.baseInfo;

  const items = [
    {
      key: 'name',
      label: '',
      children: <span>{name}</span>,
    },
    {
      key: 'website',
      label: '',
      children: (
        <a href={`//${website}`} target="_blank" rel="noreferrer">
          
        </a>
      ),
    },
    {
      key: 'manager',
      label: '',
      children: <span>{manager}</span>,
    },
    {
      key: 'employees',
      label: '',
      children: <span>{employees}</span>,
    },
    {
      key: 'area',
      label: '',
      children: (
        <span>
          {province}-{city}
        </span>
      ),
    },
    {
      key: 'exchange',
      label: '',
      children: (
        <span>
          {exchangeMap[exchange]}-{market}
        </span>
      ),
    },
    {
      key: 'list_date',
      label: '',
      children: <span>{dayjs(list_date).format('YYYYMDD')}</span>,
    },
    {
      key: 'industry',
      label: '',
      children: (
        <span>
          {l1_name}-{l2_name}-{l3_name}
        </span>
      ),
      span: 2,
    },
    {
      key: 'act_ent_type',
      label: <div style={{ minWidth: '98px' }}></div>,
      children: <span>{act_ent_type}</span>,
    },
    {
      key: 'act_name',
      label: '',
      children: <span>{act_name}</span>,
      span: 2,
    },
    {
      key: 'main_business',
      label: '',
      children: <span style={{ letterSpacing: '0.8px' }}>{main_business}</span>,
      span: 3,
    },
    {
      key: 'act_ent_type',
      label: '',
      span: 3,
      children: (
        <div>
          <Button
            type="link"
            onClick={() => {
              const [codeNumber, ex] = code.split('.');
              const code_for_dongcai = `${ex}${codeNumber}`;
              window.open(
                `https://emweb.securities.eastmoney.com/pc_hsf10/pages/index.html?type=web&code=${code_for_dongcai}&color=b#/cpbd`
              );
            }}
          >
            
          </Button>
        </div>
      ),
    },
  ];

  return (
    <div>
      <Divider orientation="left"></Divider>
      <Descriptions bordered items={items} size="small" />
    </div>
  );
};

export default BaseInfo;
